'use client';
import React, { useState } from 'react';
import Icon from '@/components/ui/Icons';

export default function Sidebar() {
  const [isDark, setIsDark] = useState(true);
  const [isPortuguese, setIsPortuguese] = useState(true);

  const menuItems = [
    { name: 'Home', href: '#home', icon: 'home' as const },
    { name: 'About', href: '#about', icon: 'user' as const },
    { name: 'Resume', href: '#resume', icon: 'fileText' as const },
    { name: 'Portfolio', href: '#portfolio', icon: 'images' as const },
    { name: 'Services', href: '#services', icon: 'layers' as const },
    { name: 'Contact', href: '#contact', icon: 'mail' as const },
  ];

  const socialLinks = [
    { name: 'Twitter', icon: 'twitter' as const, href: '#' },
    { name: 'LinkedIn', icon: 'linkedin' as const, href: '#' },
    { name: 'Instagram', icon: 'instagram' as const, href: '#' },
    { name: 'Github', icon: 'github' as const, href: '#' },
  ];

  return (
    <div className="fixed left-6 top-20 bottom-20 w-64 bg-gray-900/95 backdrop-blur-sm rounded-2xl border border-gray-700/50 flex flex-col p-4 shadow-2xl">
      
      {/* Controls - Dark/Light e PT/EN */}
      <div className="flex justify-between items-center mb-6 px-2">
        {/* Dark/Light Toggle */}
        <div className="flex items-center space-x-2">
          <Icon name="moon" size={16} className="text-gray-400" />
          <button
            onClick={() => setIsDark(!isDark)}
            className={`w-12 h-6 flex items-center rounded-full p-1 transition-all duration-300 ${
              isDark ? 'bg-blue-600 justify-end' : 'bg-gray-600 justify-start'
            }`}
          >
            <div className="w-4 h-4 bg-white rounded-full shadow-md"></div>
          </button>
          <Icon name="sun" size={16} className="text-gray-400" />
        </div>

        {/* Language Toggle */}
        <button
          onClick={() => setIsPortuguese(!isPortuguese)}
          className={`px-3 py-1 rounded-full text-sm font-medium transition-all duration-200 ${
            isPortuguese 
              ? 'bg-blue-600 text-white' 
              : 'bg-gray-700 text-gray-300 hover:bg-gray-600'
          }`}
        >
          {isPortuguese ? 'PT' : 'EN'}
        </button>
      </div>

      {/* Menu Navigation - Itens mais juntos */}
      <nav className="flex-1 flex flex-col justify-center">
        <ul className="space-y-2">
          {menuItems.map((item) => (
            <li key={item.href}>
              <a
                href={item.href}
                className="flex items-center py-2 px-3 text-gray-300 hover:bg-blue-600 hover:text-white rounded-xl transition-all duration-200 group"
              >
                <Icon 
                  name={item.icon} 
                  className="mr-3 text-gray-400 group-hover:text-white" 
                  size={30}
                />
                <span className="font-medium text-sm">{item.name}</span>
              </a>
            </li>
          ))}
        </ul>
      </nav>

      {/* Social Links - Compacto */}
      <div className="mt-auto">
        <div className="flex justify-center space-x-3">
          {socialLinks.map((social) => (
            <a
              key={social.name}
              href={social.href}
              className="w-8 h-8 flex items-center justify-center bg-gray-800/80 text-gray-400 rounded-lg hover:bg-blue-600 hover:text-white transition-all duration-200"
              title={social.name}
            >
              <Icon name={social.icon} size={17} />
            </a>
          ))}
        </div>
      </div>
    </div>
  );
}