import { 
  Home, User, FileText, Images, Layers, Mail,
  Moon, Github, Linkedin, Twitter, Instagram 
} from 'lucide-react';

export const Icons = {
  home: Home,
  user: User,
  fileText: FileText,
  images: Images,
  layers: Layers,
  mail: Mail,
  moon: Moon,
  github: Github,
  linkedin: Linkedin,
  twitter: Twitter,
  instagram: Instagram,
};

export type IconName = keyof typeof Icons;

interface IconProps {
  name: IconName;
  size?: number;
  className?: string;
}

export default function Icon({ name, size = 24, className = "" }: IconProps) {
  const LucideIcon = Icons[name];
  return <LucideIcon size={size} className={className} />;
}